"use client"

import { zodResolver } from "@hookform/resolvers/zod"
import { useForm } from "react-hook-form"
import * as z from "zod"
import { Button } from "@/components/ui/button"
import {
    Form,
    FormControl,
    FormDescription,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
} from "@/components/ui/form"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select"
import { useData, Service } from "@/lib/data-context"
import { useRouter } from "next/navigation"
import { iconMap } from "@/lib/icons"
import { useEffect } from "react"
import { toast } from "sonner"

const formSchema = z.object({
    title: z.string().min(2, {
        message: "Title must be at least 2 characters.",
    }),
    description: z.string().min(10, {
        message: "Description must be at least 10 characters.",
    }),
    iconName: z.string({
        required_error: "Please select an icon.",
    }),
    color: z.string().min(4, {
        message: "Color must be a valid hex code.",
    }),
    image: z.string().min(1, {
        message: "Image URL is required.",
    }),
    features: z.string().min(1, {
        message: "Features are required (comma separated).",
    }),
})

interface ServiceFormProps {
    initialData?: Service
    isEditing?: boolean
}

export function ServiceForm({ initialData, isEditing = false }: ServiceFormProps) {
    const { addService, updateService } = useData()
    const router = useRouter()

    const form = useForm<z.infer<typeof formSchema>>({
        resolver: zodResolver(formSchema),
        defaultValues: {
            title: initialData?.title || "",
            description: initialData?.description || "",
            // Default values for hidden fields
            iconName: initialData?.iconName || "Brain",
            color: initialData?.color || "#7B3FEF",
            image: initialData?.image || "",
            features: initialData?.features.join(", ") || "",
        },
    })

    const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0]
        if (file) {
            const imageUrl = URL.createObjectURL(file)
            form.setValue("image", imageUrl)
        }
    }

    function onSubmit(values: z.infer<typeof formSchema>) {
        const serviceData = {
            ...values,
            features: values.features.split(",").map((f) => f.trim()).filter((f) => f !== ""),
            href: initialData?.href || `/services/${values.title.toLowerCase().replace(/\s+/g, "-")}`,
        }

        if (isEditing && initialData) {
            updateService(initialData.id, serviceData)
            toast.success("Service updated successfully")
        } else {
            addService(serviceData)
            toast.success("Service created successfully")
        }

        router.push("/dashboard/services")
        router.refresh()
    }

    return (
        <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8 max-w-2xl">
                <FormField
                    control={form.control}
                    name="title"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Title</FormLabel>
                            <FormControl>
                                <Input placeholder="Service Title" {...field} />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="description"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Description</FormLabel>
                            <FormControl>
                                <Textarea placeholder="Service Description" {...field} />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />

                {/* Hidden fields for icon and color */}
                <input type="hidden" {...form.register("iconName")} />
                <input type="hidden" {...form.register("color")} />

                <FormField
                    control={form.control}
                    name="image"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Service Image</FormLabel>
                            <FormControl>
                                <div className="space-y-4">
                                    <Input
                                        type="file"
                                        accept="image/*"
                                        onChange={handleImageUpload}
                                    />
                                    {field.value && (
                                        <div className="relative w-full h-48 rounded-lg overflow-hidden border">
                                            <img
                                                src={field.value}
                                                alt="Preview"
                                                className="object-cover w-full h-full"
                                            />
                                        </div>
                                    )}
                                    {/* Hidden input to store the URL */}
                                    <input type="hidden" {...field} />
                                </div>
                            </FormControl>
                            <FormDescription>
                                Upload an image for the service card.
                            </FormDescription>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="features"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Features</FormLabel>
                            <FormControl>
                                <Textarea placeholder="Feature 1, Feature 2, Feature 3" {...field} />
                            </FormControl>
                            <FormDescription>
                                Separate features with commas.
                            </FormDescription>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <Button type="submit">{isEditing ? "Update Service" : "Create Service"}</Button>
            </form>
        </Form>
    )
}
